import { IconCart } from "components/icons";
import { useShopee } from "contexts/shopeeContext";
import { FakeDataShopee } from "data/FakeDataShopee";
import React from "react";
import { useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { v4 } from "uuid";

const InfoProduct = () => {
  const data = FakeDataShopee;
  const { addToCart, buyNow } = useShopee();
  const { slug } = useParams();
  useEffect(() => {
    document.body.scrollIntoView({ behavior: "smooth", block: "start" });
  }, []);
  if (!slug) return;

  return (
    <>
      {data.length > 0 &&
        data
          .filter((data) => data.slug === slug)
          .map((product) => {
            return (
              <div
                className="flex md:flex-row flex-col md:w-full md:p-4 p-2 bg-white rounded md:gap-x-[50px] w-screen"
                key={v4()}
              >
                <div className="md:w-[450px]  h-[450px] overflow-hidden">
                  <ImgProduct img_src={product.img_url}></ImgProduct>
                </div>
                <div className="lg:w-[720px] md:w-[500px] flex flex-col my-4">
                  <h3 className="text-[18px]  leading-5 text-[rgba(0,_0,_0,_0.8)] mb-3">
                    {product.title}
                  </h3>
                  <div className="flex mb-4 gap-x-4">
                    <div className="text-sm leading-[17px] text-[#767676] ">
                      <span className="underline text-[#222] text-[16px] leading-[19px] mr-3">
                        {product.review}
                      </span>
                      Đánh giá
                    </div>
                    <div className="text-sm leading-[17px] text-[#767676]">
                      <span className="underline text-[#222] text-[16px] leading-[19px] mr-3">
                        {product.sold}
                      </span>
                      Đã bán
                    </div>
                  </div>
                  <div className="p-5 rounded-lg bg-whiteSmoke text-[#ee4d2d] text-[30px] leading-[36px] flex gap-x-1  mb-6">
                    <div className="text-[20px] -mb-2 inline-block">đ</div>
                    <div>{product.price}</div>
                  </div>

                  <div className="flex md:mt-[100px] mt-[50px] gap-x-5">
                    <button
                      onClick={() => addToCart(product)}
                      className="flex items-center justify-center gap-x-2 bg-[#ffede9] border border-primary p-3 rounded text-primary cursor-pointer"
                    >
                      <IconCart stroke="#ee4d2d"></IconCart>
                      <span>Thêm vào giỏ hàng</span>
                    </button>

                    <div
                      onClick={() => buyNow(product)}
                      className="flex items-center justify-center p-3 text-white rounded bg-primary h-[58px]"
                    >
                      Mua ngay
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
    </>
  );
};

function ImgProduct({ img_src, alt = "product" }) {
  return <img src={img_src} alt={alt} className="object-center w-full h-full md:object-cover" />;
}

export default InfoProduct;
