import { FakeDataShopee } from "data/FakeDataShopee";
import React from "react";
import { useParams } from "react-router-dom";
import { v4 } from "uuid";

const DescProduct = () => {
  const data = FakeDataShopee;
  const { slug } = useParams();
  if (!slug) return;
  return (
    <>
      {data.length > 0 &&
        data
          .filter((data) => data.slug === slug)
          .map((product) => {
            return (
              <div className="p-5 bg-white rounded" key={v4()}>
                <Heading title="chi tiết sản phẩm"></Heading>
                <div className="flex flex-col my-6 gap-y-4">
                  <DetailsProdcut title="Danh Mục" desc="Giày"></DetailsProdcut>
                  <DetailsProdcut
                    title="Xuất xứ"
                    desc="Việt Nam"
                  ></DetailsProdcut>
                  <DetailsProdcut
                    title="Số sản phẩm còn lại"
                    desc="500"
                  ></DetailsProdcut>
                  <DetailsProdcut
                    title="Gửi từ"
                    desc="Hồ Chí Minh"
                  ></DetailsProdcut>
                </div>
                <Heading title="Mô tả sản phẩm"></Heading>
                <div className="mt-6 mb-4">
                  <Description>{product.desc}</Description>
                </div>
              </div>
            );
          })}
    </>
  );
};

function Heading({ title = "" }) {
  return (
    <h3 className="text-[18px] leading-5 text-[rgba(0,_0,_0,_0.87)] uppercase p-4 bg-whiteSmoke rounded">
      {title}
    </h3>
  );
}

function DetailsProdcut({ title = "", desc = "" }) {
  return (
    <div className="grid grid-cols-2 text-sm leading-4 capitalize w-[400px]">
      <div className="text-[rgba(0,_0,_0,_0.4)]">{title}</div>
      <div className="text-[rgba(0,_0,_0,_0.8)]">{desc}</div>
    </div>
  );
}

function Description({ children }) {
  return (
    <p className="text-sm leading-[24px] text-[rgba(0,_0,_0,_0.8)] mb-4 px-4">
      {children}
    </p>
  );
}
export default DescProduct;
