import React from "react";
import ItemFav from "./parts/ItemFav";
import ItemImage from "./parts/ItemImage";
import ItemPrice from "./parts/ItemPrice";
import ItemTagSale from "./parts/ItemTagSale";
import { Link } from "react-router-dom";
import ItemTitle from "./parts/ItemTitle";
import { useEffect } from "react";

const ItemShopee = ({ item }) => {
  return (
    <Link to={`/${item.slug}`}>
      <div className="bg-white lg:w-[200px] md:w-[190px] w-[187px] border hover:border-primary cursor-pointer transition-all h-full">
        <div className="relative ">
          <ItemImage img_url={item.img_url} alt={item.id}></ItemImage>
          {item.fav === true ? <ItemFav></ItemFav> : ""}
          <ItemTagSale sale={item.sale}></ItemTagSale>
        </div>
        <div className="p-2">
          <ItemTitle title={item.title}></ItemTitle>
          <ItemPrice amount={item.amount} price={item.price}></ItemPrice>
        </div>
      </div>{" "}
    </Link>
  );
};

export default ItemShopee;
