import ListItem from "modules/shopeeContainer/ShopeeItem/ListItem";
import React from "react";
import { useEffect } from "react";
import AuthorProduct from "./parts/AuthorProduct";
import DescProduct from "./parts/DescProduct";
import InfoProduct from "./parts/InfoProduct";

const ItemProduct = () => {
  useEffect(() => {
    document.body.scrollIntoView({ behavior: "smooth", block: "start" });
  }, []);
  return (
    <div className="flex flex-col gap-y-4">
      <InfoProduct></InfoProduct>
      <AuthorProduct></AuthorProduct>
      <DescProduct></DescProduct>
      <ListItem title="Có thể bạn cũng thích"></ListItem>
    </div>
  );
};

export default ItemProduct;
