import React from "react";


const ItemPrice = (props) => {
  const { price = "1", amount = 1 } = props;
  return (
    <div className="flex items-center justify-between">
      <div className="text-primary">
        <span className="text-[12px] leading-[14px] underline">đ</span>
        <span className="text-[16px] leading-[19px]">{price}</span>
      </div>
      <div className="text-[rgba(0,_0,_0,_0.54)] text-[12px] leading-[14px]">
        <span className="">Đã bán </span>
        <span className="text-[10px]">{amount}</span>
      </div>
    </div>
  );
};


export default ItemPrice;
