import { FakeDataShopee } from "data/FakeDataShopee";
import React, { useState } from "react";
import { useEffect } from "react";
import { v4 } from "uuid";
import ItemShopee from "./ItemShopee";

const ListItem = ({ title = "Gợi ý hôm nay" }) => {
  const data = FakeDataShopee;

  const [visibile, setVisible] = useState(12);

  const showMoreItems = () => {
    setVisible((prevValue) => prevValue + 6);
  };

  return (
    <>
      <div className="p-4 bg-white">
        <h2 className="uppercase text-primary text-[16px] leading-5 cursor-pointer">
          {title}
        </h2>
      </div>
      <List>
        {data.length > 0 &&
          data.slice(0, visibile).map((item) => {
            return <ItemShopee key={v4()} item={item}></ItemShopee>;
          })}
      </List>

      <div className="mx-auto bg-white lg:w-[400px] w-[200px] text-center border border-primary cursor-pointer hover:bg-gray-100 transition-all">
        <button
          onClick={showMoreItems}
          className={`text-[14px] leading-[17px] text-[#555] w-full h-full p-2 block`}
        >
          Xem Thêm
        </button>
      </div>
    </>
  );
};

function List({ children }) {
  return (
    <div className="grid grid-cols-2 mx-auto lg:grid-cols-6 md:grid-cols-4 lg:gap-x-4 gap-x-4 gap-y-2 lg:gap-y-4 md:gap-x-2">
      {children}
    </div>
  );
}

export default ListItem;
